* {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
  flex-wrap: wrap;
}

:root{
  --radius: 5px;
  --gray: RGB(220, 220, 220);
  --gap: 14px;
  --blue: RGB(174, 212, 255);
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@media screen and (min-width:700px){
  #root {
    display: flex;
    justify-content: center;
    background-color: var(--blue);
  }

  .app {
    display: flex;
    min-height: 100vh;
    width: 50%;
    flex-direction: column;
    background-color: white;
  }
}

@media screen and (max-width:700px){
  .app {
    display: flex;
    min-height: 100vh;
    width: 100%;
    flex-direction: column;
  }
}

.header,
.logo-container,
.main1,
.main2 {
  padding: 2rem;
}

/* general element css */
input {
  width: 100%;
  outline: none;
  border: 1px solid black;
  border-radius: var(--radius);
  font-size: .8rem;
  padding: 5px;
}

input:focus {
  background-color: var(--gray)
}

.title {
  font-style: italic;
  background-color: RGB(230, 230, 230);
  padding: 5px;
  border-radius: var(--radius);
}

a {
  color: black;
  text-decoration: none;
}
a:hover {
  text-decoration: underline;
}

button {
  width: 20%;
  font-family: inherit;
  font-weight: inherit;
  outline: none;
  border: 1px solid black;
  background-color: white;
  border-radius: var(--radius);
  padding: 5px;
}

button:active {
  background-color: var(--gray);
}

p {
  font-size: .8rem;
}
/* header */
.logo-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: var(--blue);
}

.logo {
  display: flex;
  flex-direction: column;
  gap: var(--gap);
  align-items: center;
}

.logo > h1 {
  font-size: 28px;
}

.logoImg {
  width: 80px;
  height: 80px;
  border-radius: 100%;
}

.copyright-tag {
  font-size: 10px;
  margin: 5px 0px;
}

.info {
  display: grid;
  gap: var(--gap);
}

.personal-info {
  display: grid;
  grid-template-columns: repeat(3, 1fr); 
  gap: var(--gap);
}

.personal-info-html {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  word-break: break-all;
}

.personal-btn-groups {
  display: grid;
  grid-template-columns: 1fr;
  margin-top: 10px;
  gap: var(--gap);
  justify-items: center;
}

/* main 1 */

.main1-header {
  display: flex;
  justify-content: space-between;
}

.education-form-container {
  margin: 1rem 0px;
  position: relative;
}

.education-form {
  display: grid;
  gap: var(--gap); 
  margin: 1rem 0px;
}

.education-date {
  display: grid;
  gap: var(--gap);
  grid-template-columns: 1fr 9fr;
  align-items: center;
}

.education-date > input {
  width: 100%;
}

.education-btn-group {
  display: flex;
  gap: var(--gap);
  justify-content: center;
}

.addBtn:hover,
.addBtn:active, 
.deleteIcon:hover,
.deleteIcon:active {
  transform: scale(1.1);
}

.education-form-html-element {
  display: grid;
  grid-template-columns: 1fr 3fr;
}

.deleteIcon {
  position: absolute;
  bottom: 15px;
  right: 5px;
}